/* General styles */
body {
    background-color: #f8fafc;
    color: #1a1a1a;
    margin: 0;
}

.container {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
}

/* Search and Filter styles */
.search-bar {
    display: none;
}

.filter-bar {
    display: flex;
    gap: 16px;
    padding: 16px !important;
    align-items: center;
    background-color: white;
    border-radius: 12px !important;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05) !important;
}

.search-field {
    flex: 1;
}

.format-select {
    min-width: 150px !important;
}

/* Album Card styles */
.album-cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 24px;
    padding: 16px 0;
}

.album-card {
    background: white;
    height: 100%;
    display: flex;
    flex-direction: column;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    position: relative;
}

.album-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.1) !important;
}

.album-header {
    padding: 20px 20px 12px;
}

.album-title-section {
    position: relative;
}

.album-name {
    color: #1e293b;
    font-size: 1.1rem !important;
    line-height: 1.4 !important;
    margin-bottom: 8px !important;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.album-meta {
    display: flex;
    align-items: center;
    gap: 12px;
    justify-content: space-between;
}

.album-band {
    color: #64748b !important;
    font-weight: 500 !important;
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.band-name {
    display: block;
    line-height: 1.4;
}

.album-info-section {
    padding: 12px 20px;
    background: #f8fafc;
    margin: 0;
    border-radius: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.album-year-section {
    display: flex;
    align-items: center;
    gap: 8px;
    color: #64748b;
}

.release-date {
    margin-left: 8px;
    padding-left: 8px;
    border-left: 2px solid #e2e8f0;
}

.format-section {
    display: flex;
    gap: 12px;
    align-items: center;
}

.format-icon {
    width: 24px;
    height: 24px;
    object-fit: contain;
}

.album-details {
    padding: 16px 20px;
    border-top: 1px solid #e2e8f0;
    margin-top: 12px;
}

.section-title {
    color: #64748b;
    margin-bottom: 12px !important;
    font-weight: 600 !important;
}

.genre-chips {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    margin: 8px 0;
}

.genre-chip {
    background: #eff6ff !important;
    color: #2563eb !important;
    font-weight: 500 !important;
    padding: 4px 12px !important;
}

.expand-button {
    position: absolute !important;
    top: 12px !important;
    right: 12px !important;
    background: #f8fafc !important;
    transition: all 0.2s ease !important;
}

.expand-button:hover {
    background: #e2e8f0 !important;
}

.expand-button.expanded {
    transform: rotate(180deg);
}

/* Modern Table Styles */
.album-table {
    background: white;
    border-radius: 16px !important;
    overflow: hidden;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
        0 2px 4px -1px rgba(0, 0, 0, 0.06) !important;
    margin-top: 16px;
}

.album-table .MuiTableHead-root {
    background: #f8fafc;
}

.album-table .MuiTableCell-head {
    font-weight: 600 !important;
    color: #1e293b !important;
    white-space: nowrap;
}

.sortable-header {
    cursor: pointer;
    transition: background-color 0.2s ease;
    user-select: none;
}

.sortable-header:hover {
    background-color: #f1f5f9;
}

.chip-container {
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
    max-width: 300px;
}

.table-format-icon {
    width: 20px;
    height: 20px;
    object-fit: contain;
    padding: 2px;
}

.table-format-icon:hover {
    transform: scale(1.1);
    filter: grayscale(0%) brightness(1);
}

/* Responsive Adjustments */
@media (max-width: 600px) {
    .album-cards {
        padding: 16px;
    }

    .album-card-content {
        padding: 10px !important;
    }

    .album-header {
        padding: 10px;
    }

    .album-info-section {
        padding: 10px 16px;
        gap: 12px;
    }

    .format-section {
        width: 100%;
        justify-content: flex-end;
    }

    .sticky-controls {
        padding: 12px;
    }

    .filter-bar {
        flex-direction: column;
        padding: 12px !important;
    }

    .format-select {
        width: 100% !important;
    }

    /* Add styles for the sort buttons container */
    .MobileOnlyBox {
        padding: 4px !important;
        margin: 0 !important;
        gap: 8px !important;
        background: white !important;
        border-radius: 12px !important;
    }

    /* Adjust button sizes in sort container */
    .MobileOnlyBox .MuiButton-root {
        min-width: unset !important;
        padding: 4px 12px !important;
    }

    /* Remove any extra margins */
    .MobileOnlyBox+* {
        margin-top: 0 !important;
    }
}

/* Add these styles for custom icons */
.calendar-icon {
    width: 18px;
    height: 18px;
    object-fit: contain;
    vertical-align: middle;
    margin-right: 4px;
}

/* Update search and filter styles */
.search-field-container {
    position: relative;
    flex: 1;
    display: flex;
    align-items: center;
}

.clear-button {
    position: absolute !important;
    right: 8px;
    color: #94a3b8 !important;
}

.clear-button:hover {
    color: #64748b !important;
}

.format-chips {
    display: flex;
    flex-wrap: wrap;
    gap: 4px;
}

.format-chip {
    background-color: #e2e8f0 !important;
    color: #475569 !important;
}

/* Update format select styles */
.format-select {
    min-width: 200px !important;
}

.format-select .MuiSelect-select {
    min-height: 40px !important;
}

/* Mobile adjustments */
@media (max-width: 600px) {
    .search-field-container {
        width: 100%;
    }

    .format-select {
        width: 100% !important;
    }

    .format-chips {
        max-width: calc(100% - 32px);
    }
}

/* Offline indicator styles */
.offline-indicator {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: #f44336;
    color: white;
    text-align: center;
    padding: 10px;
    z-index: 1000;
    display: none;
    justify-content: center;
    align-items: center;
    transform: translateY(-100%);
    transition: transform 0.3s ease;
}

.offline-indicator.visible {
    display: flex;
    transform: translateY(0);
}

.offline-indicator button {
    margin-left: 10px;
    padding: 4px 8px;
    background-color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    color: #f44336;
    font-weight: 500;
}

.offline-indicator button:hover {
    background-color: #eee;
}

/* Adjust sticky controls when offline banner is visible */
.offline-indicator.visible+.sticky-controls {
    margin-top: 40px;
}

/* Sticky controls */
.sticky-controls {
    position: sticky;
    top: 0;
    z-index: 999;
    background-color: #f8fafc;
    padding: 16px;
    transition: transform 0.3s ease;
}

/* Mobile styles */
@media (max-width: 600px) {
    .sticky-controls {
        position: sticky;
        padding: 8px;
        transform: translateY(0);
    }

    .sticky-controls.hidden {
        transform: translateY(-100%);
    }

    .sticky-controls.visible {
        transform: translateY(0);
    }

    /* Add padding to the content on mobile */
    .content-wrapper {
        padding-top: 60px;
    }

    /* Handle offline indicator on mobile */
    .offline-indicator.visible + .sticky-controls {
        transform: translateY(40px);
    }
}

/* Add padding to the content to prevent it from jumping when header becomes fixed */
.content-wrapper {
    padding-top: 80px; /* Adjust based on your header height */
}

/* Update the offline indicator styles */
.offline-indicator.visible + .sticky-controls {
    transform: translateY(40px); /* Height of offline indicator */
}

/* Mobile adjustments */
@media (max-width: 600px) {
    .sticky-controls {
        padding: 8px;
    }
    
    .content-wrapper {
        padding-top: 60px; /* Adjust for mobile */
    }
}

/* Add these styles to your App.css */
@media (max-width: 600px) {
    .filter-bar {
        padding: 8px !important;
    }

    .filter-bar .MuiTextField-root {
        margin: 4px !important;
    }

    .filter-bar .MuiInputBase-root {
        font-size: 14px !important;
    }

    .filter-bar .MuiInputLabel-root {
        font-size: 14px !important;
    }

    .filter-bar .MuiChip-root {
        height: 24px !important;
        font-size: 12px !important;
    }

    .filter-bar .MuiAutocomplete-root {
        min-width: unset !important;
        width: 100% !important;
    }

    .filter-bar .MuiOutlinedInput-input {
        padding: 8px 14px !important;
    }
}

.year-select {
    margin-left: 8px;
}